@font-face {
  font-family: "Changa";
  src: url("./font/Changa-VariableFont_wght.ttf");
}

@font-face {
  font-family: "Segment7";
  src: url("./font/Segment7-4Gml.otf");
}

* {
  font-family: "Changa", sans-serif;
}

#root,
body,
html {
  height: 100%;
  margin: 0;
}

#fondo {
  background: linear-gradient(135deg, #2f82c1 0%, #e1efe5 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

#iconSearch {
  width: 50px;
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.188));
  padding-left: 15px;
  transition: 0.5s;
}

#iconSearch:hover {
  filter: drop-shadow(3px 3px 2px rgba(4, 145, 253, 0.575));
}

#searchBar {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#searchBar > input[type="text"] {
  width: 100%;
  height: 50px;
  color: #565656;
  border-radius: 15px;
  box-sizing: border-box;
  border: 3px solid #ffffff;
  font-size: 25px;
  padding-left: 20px;
  transition: 0.5s;
  outline: none;
}
#searchBar > input[type="text"]:focus {
  border: 2px solid rgb(97, 143, 223);
  box-shadow: 0px 0px 20px 10px rgba(125, 184, 192, 0.443);
}

#searchBar > input::placeholder {
  color: rgb(159, 217, 158);
}

.searchText {
  width: 60%;
}

.searchText h1 {
  color: white;
  margin: 0;
  margin-left: 10px;
}

.searchText h6 {
  font-size: 20px;
  margin-top: 15px;
  margin: 15px 0;
}

.valid {
  color: rgb(45, 160, 7);
}
.errorValid {
  color: red;
}

/***   BOX  ***/

#box {
  border-radius: 15px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin: 20px;
}

/**** Tournament ****/

.titleTournaments {
  color: white;
  margin: 20px;
}

#tournament {
  display: flex;
  align-items: center;
}

#tournament > img {
  width: 120px;
}

#tournament > div {
  display: flex;
  flex-direction: column;
}

#tournament > div > * {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 15px;
  width: 50vw;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

#tournament > div > div {
  display: flex;
  justify-content: space-between;
}

#tournament > div:nth-child(2) > span:first-child {
  border-bottom: 1px solid rgba(208, 205, 205, 0.656);
}

/***** LOGIN *****/

.loginText {
  display: flex;
  flex-direction: column;
}

#login {
  display: flex;
  flex-direction: column;
  width: 50vw;
}

@media (min-width: 800px) {
  #login {
    width: 20vw;
  }
}

#login input {
  margin-bottom: 10px;
}

#login input {
  width: 100%;
  height: 30px;
  color: #565656;
  border-radius: 10px;
  box-sizing: border-box;
  border: 1px solid #90cbed;
  font-size: 15px;
  padding-left: 20px;
  transition: 0.2s;
  outline: none;
}
#login input:focus {
  border: 2px solid rgb(97, 143, 223);
}

#login input::placeholder {
  color: rgb(159, 217, 158);
}

#login button {
  height: 30px;
  border-radius: 10px;
  border: 2px solid rgb(23, 95, 218);
  background-color: #4e77c3;
  color: #ffffff;
}

/*** Matches ***/

@media (max-width: 700px) {
  .match {
    width: 80vw;
  }

  .iconWinner {
    width: 20%;
  }

  .iconLoser {
    width: 20%;
    opacity: 0.3;
    filter: grayscale();
  }
}

@media (min-width: 701px) and (max-width: 1200px) {
  .match {
    width: 70vw;
  }

  .iconWinner {
    width: 18%;
  }

  .iconLoser {
    width: 18%;
    opacity: 0.3;
    filter: grayscale();
  }
}

@media (min-width: 1201px) {
  .match {
    width: 60vw;
  }

  .iconWinner {
    width: 13%;
  }

  .iconLoser {
    width: 13%;
    opacity: 0.3;
    filter: grayscale();
  }
}

.match > div:nth-child(odd) {
  text-align: end;
  padding: 10px 30px 10px 0;
}

.result {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.result > img {
  flex: 0;
  width: 100px;
}

.result > * {
  flex: 1;
}

.result > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.result p:first-child {
  font-size: 25px;
  margin: 0;
  height: 40px;
}

.result > div > span {
  display: flex;
  justify-content: center;
}

/**** - score - ********/

.scoreWin {
  font-family: "Segment7";
  font-size: 60px;
  color: rgb(29, 135, 12);
}

.scoreLoser {
  font-family: "Segment7";
  font-size: 60px;
  color: rgb(135, 12, 43);
}

/****** - edit - ******/

.games {
  display: flex;
  align-items: center;
}

.games > * {
  margin-left: 26px;
}

.matchEdit > div:nth-child(3) > div:first-child {
  display: flex;
  flex-direction: column;
}

.matchEdit > div:nth-child(2) {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.matchEdit > div:nth-child(3) {
  display: flex;
  align-items: center;
}

.matchEdit > div:nth-child(3) > img {
  width: 70px;
  height: 70px;
}

.matchEditSet {
  display: flex;
}

.matchEditSet > .buttonSet {
  margin: 10px;
}

.setGames {
  margin: 10px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/**** - option - ****/

.option-image {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

/*** - selectCharacter - ***/

.container-selectCharacter {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.selectCharacter {
  position: absolute;
  border-radius: 15px;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  overflow-x: hidden;
  height: 80%;
  width: 80%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  z-index: 10000;
}

.selectCharacter > div > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}
.selectCharacter > div > div > div {
  width: 85px;
  padding: 5px;
  margin: 5px;
}
.selectCharacter img {
  width: 60px;
}

.character {
  display: flex;
  flex-direction: column;
  box-shadow: inset 0px 0px 20px 1px rgb(237 237 237);
  border-radius: 15px;
  padding: 5px;
  margin: 15px;
  width: 85px;
  height: 100px;
  justify-content: center;
  align-items: center;
}

.character > div {
  width: 85px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}

.buttonSet {
  width: 60px;
  height: 60px;
  border-radius: 10px;
  box-shadow: inset 0px 0px 14px 1px rgb(0 0 0 / 18%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttonSet_3 {
  background-color: #57fd576b;
}

.buttonSet > img {
  width: 40px;
}

.buttonWin {
  background-color: #57fd576b;
}

.buttonLoss {
  background-color: #e31b2c5f;
}

.buttonDqSelect {
  background-color: #2b87ce6b;
}

/**** - dq - ****/

.dq {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.dq > div {
  margin-left: 20px;
}

.buttonMatch {
  width: 111px;
  height: 40px;
  border-radius: 10px;
  box-shadow: inset 0px 0px 14px 1px rgb(0 0 0 / 18%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.buttonSet:disabled{

  pointer-events: none;
  
}

/***** -- Save - cancel -- *****/

.buttonCancel {
  width: 120px;
  height: 40px;
  border-radius: 10px;
  background-color: #c21840;
  color: white;
  box-shadow: inset 0px 0px 14px 1px rgb(0 0 0 / 18%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.buttonSave {
  width: 120px;
  height: 40px;
  border-radius: 10px;
  background-color: #1e74b5;
  color: white;
  box-shadow: inset 0px 0px 14px 1px rgb(0 0 0 / 18%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-left: 30px;
}

.footerMcatchEdit {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 20px;
  margin-right: 10px;
}

@media (max-width: 480px) {
  .matchEdit > div:nth-child(3) > img {
    width: 40px;
    height: 40px;
  }
  .matchEdit > div:nth-child(2) {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .dq {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .dq > div {
    margin-left: 0px;
  }
  .buttonSet {
    width: 50px;
    height: 50px;
    border-radius: 10px;
    box-shadow: inset 0px 0px 14px 1px rgb(0 0 0 / 18%);
    display: flex;
    align-items: center;
  }

  .matchEdit > div:nth-child(3) {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .matchEditSet > .buttonSet {
    margin: 5px;
  }

  .setGames {
    margin: 10px;
    height: 40px;
  }

  .selectCharacter > div > div > div {
    width: 60px;
    height: 90px;
    padding: 5px;
    margin: 6px;
}
.selectCharacter img {
  width: 50px;
}

.character > div {
  width: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  font-size: 14px;
}
}
